import { GlobalSubscriptionService } from './../../../../app/globalSubcription.service';
import { Component, OnInit } from '@angular/core';
import { DialogRef, ModalComponent, CloseGuard } from 'angular2-modal';
import { Modal,BSModalContext} from 'angular2-modal/plugins/bootstrap';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { HttpUserAuthUtilsService } from "../../../../core/http_wrapper/http-userauth-utils.service";
import { Constants, MixpanelEvent } from '../../../../core/constants';
import { FavrotiteTrainersDetail } from "../../../../core/models/favrouiteTrainerResponse";
import { LocalStorageService } from "../../../../core/service/local-storage.service";
import { ModelpopupLrComponent } from "../../../../loginRegister/modelpopup-lr/modelpopup-lr.component";
import { AllTrainerResponseDetail } from "../../../../core/models/alltrainerResponse";
import { ErrorpopupmodelComponent } from "../../../../common/errorpopupmodel/errorpopupmodel.component";
import { MixpanelService } from '../../../../core/mixpanelService.service';

declare var $: any;



export class AdditionPopupWindowData extends BSModalContext {
   public message:string;
   public title:string;
   public tainerId:any;
   public trainerName: any;
   



}

@Component({
  selector: 'app-unfavoritePopup',
  templateUrl: './unfavoritePopup.component.html',
  styleUrls: ['./unfavoritePopup.component.css']
})

export class UnfavoriteConfirmPopup implements CloseGuard, ModalComponent<AdditionPopupWindowData>,OnInit {
  
  context: AdditionPopupWindowData;
  showLoader = false;
  message: string;
  title: string;
  tainerId: any;
  selectedTrainerId: number;
  selectedTrainerName: string;
  allTrainerList: AllTrainerResponseDetail[] = [];

 

  beforeDismiss(): boolean | Promise<boolean> {
    throw new Error("Method not implemented.");
  }
  beforeClose(): boolean | Promise<boolean> {
    throw new Error("Method not implemented.");
  }
  constructor(public modal: Modal, public dialog: DialogRef<AdditionPopupWindowData>, private mixpanel: MixpanelService,
  private http_user: HttpUserAuthUtilsService, private globalSubscriptionService: GlobalSubscriptionService) {
  this.context = dialog.context;
  this.message = this.context.message;
  this.title = this.context.title;
  this.tainerId = this.context.tainerId;
  this.selectedTrainerName = this.context.trainerName;


   }

  ngOnInit() {
  }


  ngAfterViewInit() {
       $(".modal-dialog ").css("max-width", "400px");
    }

  closePopup(){
      this.dialog.close();
  }
     errorPopupModalMessage(status,message) {
              return this.modal.open(ErrorpopupmodelComponent, overlayConfigFactory({status, message}, BSModalContext));
           }
    
   markTrainerAsFavourite(){
    this.selectedTrainerId=this.tainerId;
    if(LocalStorageService.getIsLoggedIn()){
       this.http_user.markTrainerAsUnFavourit(this.tainerId, LocalStorageService.getUserId(), this,true);
        
    }else{
      return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));}
       
  }
  onSuccess(type: any, responsedata: any) {
      if (type === Constants.MarkTrainerAsUnFavrourite) {
        this.mixpanel.logEventWithAnalytics(MixpanelEvent.removeFromFavourite, { 'origin': 'User_Profile' , 'Source' : 'web' ,
      'Trainer_Name': this.selectedTrainerName});
        this.allTrainerList.forEach(element => {
            if (element.id === this.selectedTrainerId) {
                   element.isfollow = false;
                  }
         });
                this.closePopup();
                this.globalSubscriptionService.userFavoritePopUp('favorite');
  }
  }
  onFailure(type: any, response: string) {
    if(type == Constants.MarkTrainerAsUnFavrourite){
           this.errorPopupModalMessage("Failed to mark UnFavourite",response);

    }
  }   
}



