import { EventEmitter } from '@angular/core';
import { Component, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-userfinished',
  templateUrl: './userfinished.component.html',
  styleUrls: ['./userfinished.component.css']
})
export class UserfinishedComponent implements OnInit {
  finishedCount:any;
  @Output() finishclasscount = new EventEmitter()
  constructor() { }

  ngOnInit() {
  }
  finishedcount(event){
    this.finishedCount=event+"";
    this.finishclasscount.next(this.finishedCount);
  }

}



