import { MemberShipExtensionComponent } from './../../useroptions/membershipExtension/membershipextension.component';
import { MixpanelService } from './../../../core/mixpanelService.service';
import { element } from 'protractor';
import { UserPackageDetailDtoList } from './../../../core/models/userprofile';
import { LocalStorageService } from './../../../core/service/local-storage.service';
import { UserLogin } from '../../../core/models/userloginresponse';
import { HttpSuccesFailureResponse } from '../../../core/http_wrapper/http_wrapper_response';
import { Component, OnInit } from '@angular/core';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { UserProfile } from "../../../core/models/userprofile";
import { CountryData } from "../../../core/models/countrydata";
import { HttpUserAuthUtilsService } from "../../../core/http_wrapper/http-userauth-utils.service";
import { StorageService } from "../../../core/service/storage.service";
import { Constants, MixpanelEvent } from '../../../core/constants';
import { UserSession } from "../../../core/sessions";
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { ConfirmpopupComponent } from 'common/confirmpopup/confirmpopup.component';


declare var $: any;
declare var jQuery: any;


@Component({
  selector: 'app-userpersonaldetails',
  templateUrl: './userpersonaldetails.component.html',
  styleUrls: ['./userpersonaldetails.component.css']
})
export class UserpersonaldetailsComponent implements OnInit, HttpSuccesFailureResponse {
  creditAvailable:any;
  private userdata: UserLogin;
  private profiledata: UserProfile;
  userDetails: any = LocalStorageService.getUser().data;
  userType: number = 2;
  userImage: string;
  firstname: string;
  lastname: string;
  trainers: any[];
  diffdate: any;
  checknullbalance = false;
  uploadImageProgess: boolean = false;
  private list: CountryData[];
  private subscription: Subscription;
  currencyCode: any;
  isSubscriptionActive=false;
  showRenewBtn=false;
  packageIdCurr;
  isPrime=UserSession.getDynamicElements(Constants.isPrime)=="Yes"?true:false;

  constructor(private http_user: HttpUserAuthUtilsService, private storageService: StorageService,
    private router: Router, private mixpanel: MixpanelService, public modal: Modal
  ) { }


  togglememberShip(packageObj: any) {
    const parent = this;
    let isSubscriptionActive = this.isSubscriptionActive;
    return this.modal.open(MemberShipExtensionComponent, overlayConfigFactory({packageObj, parent,isSubscriptionActive}, BSModalContext));
  }
  ngOnInit() { 
    this.currencyCode = LocalStorageService.getCurrencyCode();
    
    if(this.userDetails.walletDto){
      this.creditAvailable = this.userDetails.walletDto.balance;
    }
    this.http_user.getUserProfile(LocalStorageService.getUserId(), this, true);

  }
  routeTomember() {
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.openMembership, { 'origin': 'Recharge', 'Source': 'web' })
    this.router.navigate(['membership'], { fragment: 'membership' })
  }
  userFullDetail(data) {
    this.firstname = data.data.firstName;
    this.userImage = data.data.profileImage;
    this.userdata = this.storageService.read<UserLogin>(Constants.userDataKey);
    this.userDetails = LocalStorageService.getUser().data;
    if (this.userDetails.userPackageDetailDtoList != undefined) {
      this.userDetails.userPackageDetailDtoList.sort(function (a, b) {
        var nameA = a.packageType.toLowerCase(), nameB = b.packageType.toLowerCase()
        if (nameA > nameB) //sort string ascending
          return -1
        if (nameA < nameB)
          return 1
        return 0 //default return value (no sorting)
      });

      this.userDetails.userPackageDetailDtoList.forEach(element => {
        if (element.balanceClass == 0) {
          this.checknullbalance = true
        }
        try {
          
          var expiry_date;
          let datestr = element.expiryDate.split("-");
          expiry_date = new Date(datestr[0] + "/" + datestr[1] + "/" + datestr[2] + ' 23:59:59');
          var current_date = new Date();
          element.remainingDays = this.daysDifference(current_date, expiry_date);
          //element.remainingDays = UserSession.getdaysdiffrence1(datestr[0]+"/"+datestr[1]+"/"+datestr[2]);
        }
        catch (e) {
          console.log(e);
          element.remainingDays = undefined;
        }
      });
    }

    this.showRenewBtn = this.userDetails.userPackageDetailDtoList[0].balance<=10 ? true : false;
    this.packageIdCurr = this.userDetails.userPackageDetailDtoList[0].packageId;
  }

  renewSubscription(action){
    switch(action){
      case 'RENEW':
        this.http_user.editSubscription(this.packageIdCurr,'RENEW',LocalStorageService.getUserId(),this,true);
        break;
      case 'EXTEND':
        this.http_user.editSubscription(this.packageIdCurr,'EXTEND',LocalStorageService.getUserId(),this,true);
        break;
      
        default:
      
    }
  }

  userAlertDontClick(){
    var message = "Your payment is processing in the background. Please do not click anywhere on the page";
    var title = "Under Process";
    var errorCode = "999";
    var parent = this;

    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title, errorCode, parent }, BSModalContext));
   
  }

  onSuccess(type: any, responsedata: any) {
    if (type == Constants.FindUserApi) {
      LocalStorageService.setUser(responsedata);
      this.mixpanel.updateUserInfoToMixpanel(responsedata.data);
      this.userFullDetail(responsedata);
      if(this.userDetails.walletDto){
        this.creditAvailable = this.userDetails.walletDto.balance;
      }
      if(this.userDetails.userPackageDetailDtoList && this.userDetails.userPackageDetailDtoList[0].subscriped == true && 
        (this.userDetails.userPackageDetailDtoList[0].subscriptionStatus=='ACTIVE' || this.userDetails.userPackageDetailDtoList[0].subscriptionStatus=='TRAIL') ) {
          this.isSubscriptionActive = true;
        }
        if(UserSession.getDynamicElements(Constants.isPrime)=="Yes"){
          this.userDetails.userPackageDetailDtoList = this.userDetails.userPackageDetailDtoList.filter(element => element.packageType != 'GENERAL');
        }
    }

    if(type == Constants.editSubscriptionAPI){ 

      let transactionId = responsedata.data.referenceId;
      let isComplete = responsedata.data.isComplete;
      if(responsedata.data.length>0 && !isComplete){
       
        let startTime = (new Date()).getTime()/1000;
         this.userAlertDontClick();
         setTimeout(() => {
                   
          if(((new Date()).getTime()/1000 - startTime)<=60 ){
            this.http_user.getTransactionDetail(transactionId,this);
          }
          else{
            let url = '/paymentconfirmation/'+ transactionId;
            window.location.assign(url);
          }
    
        
         }, 10000);
      }
      else{
        this.ngOnInit();
      }
    }
  
    if(type == Constants.TransactionDetailApi){
      console.log(responsedata)
  
      if(responsedata.status == 'SUCCESS'){
        this.ngOnInit();
      }
      else{
        let url = '/paymentconfirmation/'+ responsedata.referenceId;
        window.location.assign(url);
      }
  
    
  
    }
  }
  onFailure(type: any, response: string) {
  }


  walletPopupinfo(){
    var message = "Use your wallet credits to purchase any package";
    var title = UserSession.getDynamicElements(Constants.GymName)+" Wallet";
    var errorCode = "999";
    var parent = this;

    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title, errorCode, parent }, BSModalContext));
   
  }

  isLoggedIn() {
    return UserSession.isLoggedIn();
  }
  daysDifference(date1, date2) {
    // The number of milliseconds in one day
    var ONE_DAY = 1000 * 60 * 60 * 24

    // Convert both dates to milliseconds
    var date1_ms = date1.getTime()
    var date2_ms = date2.getTime()

    // Calculate the difference in milliseconds
    var difference_ms = Math.abs(date1_ms - date2_ms)


    // Convert back to days and return
    return Math.floor(difference_ms / ONE_DAY)
  }

  navigate(gymId, gymName) {
    sessionStorage.setItem("gym_id", gymId);
    this.router.navigate(['gym', gymId, gymName])

  }

}



