import { Component, OnInit } from '@angular/core';
import { UserSession } from '../../../core/sessions';

@Component({
  selector: 'app-useractivityhistory',
  templateUrl: './useractivityhistory.component.html',
  styleUrls: ['./useractivityhistory.component.css']
})
export class UseractivityhistoryComponent implements OnInit {
  favouriteTrainerLength:any="0";
  finishedcountlength:any="0";
  constructor() { }

  ngOnInit() {
  }
  favoriteupdate(event) {
    this.favouriteTrainerLength =event+"";
  }
  finishedcount(event) {
    this.finishedcountlength = event+"";
  }

}



