import {ClassDetail} from '../../../core/models/classListResponse';
import { EventEmitter } from '@angular/core';
import { LocalStorageService } from '../../../core/service/local-storage.service';
import { Router } from '@angular/router';
import { HttpUserAuthUtilsService } from '../../../core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from './../../../core/http_wrapper/http_wrapper_response';
import { Component, OnInit, Output } from '@angular/core';
import { Constants } from '../../../core/constants';

@Component({
  selector: 'app-userfinishedclass',
  templateUrl: './userfinishedclass.component.html',
  styleUrls: ['./userfinishedclass.component.css']
})
export class UserfinishedclassComponent implements OnInit, HttpSuccesFailureResponse {
  @Output() finishedfavcount = new EventEmitter();
  finishedClasses: ClassDetail[] = [];
  ClassesListSize = 0;
   currentPage = 1;
  isLoadMoreData = true;
  lastScrollDistance = 0;
  listIsLaoding = false;
  isLoaded = false;
  currency:any;

  constructor(private http_user: HttpUserAuthUtilsService,
              private router: Router) { }

  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();
    this.listIsLaoding = true;
    this.http_user.userFinishedClasses(LocalStorageService.getUserId(), this.currentPage, this, true);
}
  navigateto(value) {
    sessionStorage.setItem('gym_id', value.gymId);
    sessionStorage.setItem('branch_id', value.branchId);
    this.router.navigate(['gym', value.gymId, Date.now()])
  }
      routeToTrainerDetails(value) {
        sessionStorage.setItem('trainer_id', value.trainerDetail.id + '');
        this.router.navigate(['/trainer', value.trainerDetail.id, Date.now()]);


  }

  onSuccess(type: any, responsedata: any) {
    if (type == Constants.UserFinishedClassesApi) {
          this.listIsLaoding = false;
    const temp_list: any[] = responsedata.data;
    temp_list.forEach(element => {
     this.finishedClasses.push(element);
   });
  if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }
    this.ClassesListSize = this.finishedClasses.length;
    this.finishedfavcount.next(this.ClassesListSize)
    }
  }
  onFailure(type: any, response: string) {
    if (type === Constants.UserFinishedClassesApi) {
    }
     }
      onScrollUp(event) {
        // if(this.isLoadMoreData && this.lastScrollDistance<event.currentScrollPosition && !this.listIsLaoding){
                  //  this.listIsLaoding=true;

          //   this.lastScrollDistance=event.currentScrollPosition;
        //   this.currentPage=this.currentPage+1;
        //       this.http_user.userFinishedClasses(LocalStorageService.getUserId(),this.currentPage,this,false);


        // }
      }
      resetBeforeLoad() {
        this.currentPage = 1;
        this.isLoadMoreData = true;
        this.finishedClasses = [];

      }

  }




