import { Output } from '@angular/core';
import { GlobalSubscriptionService } from './../../../app/globalSubcription.service';
import { HttpSuccesFailureResponse } from '../../../core/http_wrapper/http_wrapper_response';
import { LocalStorageService } from '../../../core/service/local-storage.service';
import { UserSession } from '../../../core/sessions';
import { Router } from '@angular/router';
import { StorageService } from '../../../core/service/storage.service';
import { HttpUserAuthUtilsService } from '../../../core/http_wrapper/http-userauth-utils.service';
import { UserProfile } from '../../../core/models/userprofile';
import { UserLogin } from '../../../core/models/userloginresponse';
import { Component, OnInit, EventEmitter } from '@angular/core';

import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { FavrotiteTrainersDetail } from '../../../core/models/favrouiteTrainerResponse';
import { Constants } from '../../../core/constants';
import { AllTrainerResponseDetail } from '../../../core/models/alltrainerResponse';
import { ModelpopupLrComponent } from '../../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { BSModalContext, Modal } from 'angular2-modal/plugins/bootstrap';
import { overlayConfigFactory } from 'angular2-modal';
import { UnfavoriteConfirmPopup } from '../../userdetailsmodule/userfavtrainers/unfavritePop/unfavoritePopup.component';


@Component({
  selector: 'app-userfavtrainers',
  templateUrl: './userfavtrainers.component.html',
  styleUrls: ['./userfavtrainers.component.css']
})
export class UserfavtrainersComponent implements OnInit, HttpSuccesFailureResponse {
  @Output() updatefavoritecount = new EventEmitter();

  trainers: FavrotiteTrainersDetail[] = [];
  showLoader = false;
  trainerListSize = 0;
  selectedTrainerId: number;
  subscription: Subscription;
  isLoaded = false;
  currentPage = 1;
  isLoadMoreData = true;
  lastScrollDistance = 0;
  listIsLaoding = false;
  public checkoutCommunity="Check out the community on "+UserSession.getDynamicElements(Constants.GymName)

  constructor(private http_user: HttpUserAuthUtilsService,
    private router: Router, private modal: Modal, private globalSubscriptionService: GlobalSubscriptionService) { }

  ngOnInit() {
    this.subscription = this.globalSubscriptionService.userfavoriteState
      .subscribe((favorite) => {
        this.userfavritetrainerlist();

      });
    this.userfavritetrainerlist();
  }
  userfavritetrainerlist() {
    this.listIsLaoding = true;
    // this.http_user.favTrainerList(LocalStorageService.getUserId(),this.currentPage,this,true);
    this.http_user.getFollowedusers('Followed', LocalStorageService.getUserId(), this, true);

  }

  openconfirmpopup(message, title, tainerId, trainerName) {
    return this.modal.open(UnfavoriteConfirmPopup, overlayConfigFactory({ message, title, tainerId, trainerName }, BSModalContext));
  }
  openfavoritePopup(value: FavrotiteTrainersDetail) {
    this.openconfirmpopup('Are you sure you want to remove <b> ' + value.firstName + '</b> ?', 'unfavourite', value.id, value.firstName);

  }
  onSuccess(type: any, responsedata: any) {
    if (type === Constants.favtrainertype) {
      this.trainers = [];
      this.listIsLaoding = false;
      const temp_list: FavrotiteTrainersDetail[] = responsedata.data;
      temp_list.forEach(element => {
        this.trainers.push(element);
      });
      this.trainerListSize = this.trainers.length;
      if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }
      this.updatefavoritecount.next(this.trainerListSize)
    }
  }
  onFailure(type: any, response: string) {
    if (type == Constants.favtrainertype) {
    }
  }
  onScrollUp(event) {
    // if(this.isLoadMoreData && this.lastScrollDistance<event.currentScrollPosition && !this.listIsLaoding){
    //  this.listIsLaoding=true;

    //   this.lastScrollDistance=event.currentScrollPosition;
    //   this.currentPage=this.currentPage+1;
    //   this.http_user.favTrainerList(LocalStorageService.getUserId(),this.currentPage,this,false);

    // }
  }
  resetBeforeLoad() {
    this.currentPage = 1;
    this.isLoadMoreData = true;
    this.trainers = [];

  }
  routeToTrainerDetails(value) {
    if (LocalStorageService.getUserType() == 1) {
      sessionStorage.setItem('trainer_id', value.id + '');
      this.router.navigate(['/trainer', value.id, Date.now()]);

    }
  }

  redirect_trainer() {
    this.router.navigate(['/trainers']);
  }
}





