import { MemberShipExtensionModule } from './../useroptions/membershipExtension/membershipextension.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';

import { UserdetailsmoduleComponent } from './userdetailsmodule.component';
import { UseractivityhistoryComponent } from './useractivityhistory/useractivityhistory.component';
import { UserfavtrainersComponent } from './userfavtrainers/userfavtrainers.component';
import { UnfavoriteConfirmPopup } from './userfavtrainers/unfavritePop/unfavoritePopup.component';
import { UserfinishedComponent } from './userfinished/userfinished.component';
import { UserfinishedclassComponent } from './userfinishedclass/userfinishedclass.component';
import { UserfinishedclasssuggestionComponent } from './userfinishedclasssuggestion/userfinishedclasssuggestion.component';
import { UserpersonaldetailsComponent } from './userpersonaldetails/userpersonaldetails.component';
import { routing } from './userdetails.routing';
import { CoreModule } from '../../core/core.module';
import { ModalModule } from 'angular2-modal';
import { BootstrapModalModule } from 'angular2-modal/plugins/bootstrap';
import { CoreModuleNew } from 'newcore/new-core.module';

@NgModule({
  imports: [
    routing,
    CommonModule,
    InfiniteScrollModule,
    CoreModule,
     ModalModule.forRoot(),
    BootstrapModalModule,
    MemberShipExtensionModule,
    CoreModuleNew
  ],
  declarations: [
    UserdetailsmoduleComponent,
    UseractivityhistoryComponent,
    UserfavtrainersComponent,
    UnfavoriteConfirmPopup,
    UserfinishedComponent,
    UserfinishedclassComponent,
    UserfinishedclasssuggestionComponent,
    UserpersonaldetailsComponent
  ],
  exports:[
    UnfavoriteConfirmPopup
  ]
  ,
  entryComponents: [
    UnfavoriteConfirmPopup
  ]
})
export class UserDetailsModule { }



